.products-gift {
  display: grid;
  align-content: end;
  justify-content: center;
  height: 289px;
  padding: 0;
  margin: 0 auto;
  margin-bottom: 32px;
  background-color: $basic-green;
  background-image: url("../img/gift-photo-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/gift-photo-mobile@2x.jpg");
  }

  @media (min-width: $tablet-width) {
    width: 708px;
    height: 200px;
    margin-bottom: 77px;
    background-image: url("../img/gift-photo-tablet@1x.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/gift-photo-tablet@2x.jpg");
    }
  }

  @media (min-width: $desktop-width) {
    width: 245px;
    height: 289px;
    margin: 0;
    background-image: url("../img/gift-photo-desktop@1x.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/gift-photo-desktop@2x.jpg");
    }
  }
}

.products-gift__text {
  font-family: "Arial", sans-serif;
  color: $white;
  text-align: center;
  font-size: 16px;
  line-height: 20px;
  width: 161px;
  margin: 0;
  margin-bottom: 43px;

  @media (min-width: $tablet-width) {
    text-align: left;
    margin-bottom: 68px;
    margin-left: 300px;
  }

  @media (min-width: $desktop-width) {
    text-align: center;
    margin: 0;
    margin-bottom: 43px;
  }
}

.products-gift__icon {
  fill: $white;
  height: 61px;
  margin-left: 42px;
  margin-bottom: 62px;

  @media (min-width: $tablet-width) {
    margin-left: -45px;
    margin-bottom: -53px;
  }

  @media (min-width: $desktop-width) {
    margin: 0;
    padding-left: 43px;
    padding-bottom: 62px;
  }
}
